/* IMPORT */
import isObservable from '../methods/is_observable.js';
import useGuarded from '../hooks/use_guarded.js';
import useUntracked from '../hooks/use_untracked.js';
import { ternary } from '../oby.js';
import { isComponent, isFunction, isTruthy } from '../utils/lang.js';
/* MAIN */
//TODO: Support an is/guard prop, maybe
const If = ({ when, fallback, children }) => {
    if (isFunction(children) && !isObservable(children) && !isComponent(children)) { // Calling the children function with an (() => Truthy<T>)
        const truthy = useGuarded(when, isTruthy);
        return ternary(when, useUntracked(() => children(truthy)), fallback);
    }
    else { // Just passing the children along
        return ternary(when, children, fallback); //TSC
    }
};
/* EXPORT */
export default If;
