/* IMPORT */
import Portal from '../components/portal.js';
import SuspenseCollector from '../components/suspense.collector.js';
import useEffect from '../hooks/use_effect.js';
import useRoot from '../hooks/use_root.js';
import $$ from './SS.js';
/* MAIN */
//TODO: Implement this properly, without relying on JSDOM or stuff like that
const renderToString = (child) => {
    return new Promise(resolve => {
        useRoot(dispose => {
            $$(SuspenseCollector.wrap(suspenses => {
                const { portal } = Portal({ children: child }).metadata;
                useEffect(() => {
                    if (suspenses.active())
                        return;
                    resolve(portal.innerHTML);
                    dispose();
                }, { suspense: false });
            }));
        });
    });
};
/* EXPORT */
export default renderToString;
