/* IMPORT */
import { OWNER } from '../context.js';
import { lazyArrayPush } from '../lazy.js';
import Owner from './owner.js';
/* MAIN */
class Context extends Owner {
    /* CONSTRUCTOR */
    constructor(context) {
        super();
        /* VARIABLES */
        this.parent = OWNER;
        this.context = { ...OWNER.context, ...context };
        lazyArrayPush(this.parent, 'D', this);
    }
    /* API */
    E(fn) {
        return super.E(fn, this, undefined);
    }
}
/* EXPORT */
export default Context;
