/* IMPORT */
import { SYMBOL_SUSPENSE_COLLECTOR } from '../constants.js';
import useMemo from '../hooks/use_memo.js';
import $ from '../methods/S.js';
import { context, resolve } from '../oby.js';
/* MAIN */
// Keeping track of all Suspense instances below it, needed in some cases
const SuspenseCollector = {
    create: () => {
        const parent = SuspenseCollector.get();
        const suspenses = $([]);
        const active = useMemo(() => suspenses().some(suspense => suspense.active()));
        const register = (suspense) => { parent?.register(suspense); suspenses(prev => [...prev, suspense]); };
        const unregister = (suspense) => { parent?.unregister(suspense); suspenses(prev => prev.filter(other => other !== suspense)); };
        const data = { suspenses, active, register, unregister };
        return data;
    },
    get: () => {
        return context(SYMBOL_SUSPENSE_COLLECTOR);
    },
    wrap: (fn) => {
        const data = SuspenseCollector.create();
        return context({ [SYMBOL_SUSPENSE_COLLECTOR]: data }, () => {
            return resolve(() => fn(data));
        });
    }
};
/* EXPORT */
export default SuspenseCollector;
