/* IMPORT */
import { setBatch } from '../context.js';
import { noop } from '../utils.js';
/* HELPERS */
let M = 0;
let resolve = noop;
/* MAIN */
const batch = async (fn) => {
    if (!M) {
        setBatch(new Promise(r => resolve = r));
    }
    try {
        M += 1;
        return await fn();
    }
    finally {
        M -= 1;
        if (!M) {
            setBatch(undefined);
            resolve();
        }
    }
};
/* MAIN */
export default batch;
