/* IMPORT */
import useMemo from './use_memo.js';
import $$ from '../methods/SS.js';
import { isNil } from '../utils/lang.js';
/* MAIN */
//TODO: Maybe port this to oby, as "when" or "is" or "guarded"
//TODO: Optimize this, checking if the value is actually potentially reactive
const useGuarded = (value, guard) => {
    let valueLast;
    const guarded = useMemo(() => {
        const current = $$(value);
        if (!guard(current))
            return valueLast;
        return valueLast = current;
    });
    return () => {
        const current = guarded();
        if (isNil(current))
            throw new Error('The value never passed the type guard');
        return current;
    };
};
/* EXPORT */
export default useGuarded;
