/* IMPORT */
import { OBSERVABLE_FALSE, OBSERVABLE_TRUE } from '../constants.js';
import cleanup from './cleanup.js';
import effect from './effect.js';
import isObservableFrozen from './is_observable_frozen.js';
import memo from './memo.js';
import untrack from './untrack.js';
import warmup from './warmup.js';
import { readable } from '../objects/callable.js';
import Observable from '../objects/observable.js';
import { is } from '../utils.js';
/* HELPERS */
class DisposableMap extends Map {
    constructor() {
        super(...arguments);
        this.disposed = false;
    }
}
class SelectedObservable extends Observable {
    constructor() {
        super(...arguments);
        this.AE = 1;
    }
    /* API */
    call() {
        if (this.AF.disposed)
            return;
        this.AE -= 1;
        if (this.AE)
            return;
        this.AF.delete(this.AG);
    }
}
/* MAIN */
const selector = (AG) => {
    /* NORMALIZING SOURCE */
    AG = warmup(memo(AG));
    /* FROZEN SOURCE */
    if (isObservableFrozen(AG)) {
        const sourceValue = untrack(AG);
        return (value) => {
            return (value === sourceValue) ? OBSERVABLE_TRUE : OBSERVABLE_FALSE;
        };
    }
    /* SELECTEDS */
    let AF = new DisposableMap();
    let selectedValue = untrack(AG);
    effect(() => {
        const valuePrev = selectedValue;
        const valueNext = AG();
        if (is(valuePrev, valueNext))
            return;
        selectedValue = valueNext;
        AF.get(valuePrev)?.set(false);
        AF.get(valueNext)?.set(true);
    }, { suspense: false, sync: true });
    /* CLEANUP ALL */
    const cleanupAll = () => {
        AF.disposed = true;
    };
    cleanup(cleanupAll);
    /* SELECTOR */
    return (value) => {
        /* INIT */
        let selected = AF.get(value);
        if (selected) {
            selected.AE += 1;
        }
        else {
            selected = new SelectedObservable(value === selectedValue);
            selected.AF = AF;
            selected.AG = value;
            AF.set(value, selected);
        }
        /* CLEANUP */
        cleanup(selected);
        /* RETURN */
        return readable(selected);
    };
};
/* EXPORT */
export default selector;
